/*
* 当前为配置路由文件---api下的pt-nav.js文件为执行分发路由文件
* */

import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',                                          component: resolve => require(['@/components/index/index'], resolve)},
    {path: '/login',                                     component: resolve => require(['@/components/login/login'], resolve)},
    {path: '/hospitalMenu',                              component: resolve => require(['@/components/menu/hospitalMenu'], resolve)},
    {path: '/expertMenu',                                component: resolve => require(['@/components/menu/expertMenu'], resolve)},
    {path: '/allocatingMenu',                            component: resolve => require(['@/components/menu/allocatingMenu'], resolve)},
    {path: '/releaseMenu',                               component: resolve => require(['@/components/menu/releaseMenu'], resolve)},
    {path: '/manageMenu',                                component: resolve => require(['@/components/menu/manageMenu'], resolve)},     //管理端不需要这个菜单页面。由于是懒加载。可以不用删除。页面运行中不会执行
    {path: '/outlayMenu',                                component: resolve => require(['@/components/menu/outlayMenu'], resolve)},
    {
      path: '/hospital',//医院端
      component: resolve => require(['@/components/hospital/hospital'], resolve),
      children: [
        {path: '/hospital',                              component: resolve => require(['@/components/hospital/children/routine/addRecord'], resolve)},           //常规病历
        {path: '/hospital/addRecord',                    component: resolve => require(['@/components/hospital/children/routine/addRecord'], resolve)},           //常规病历---新建病历
        {path: '/hospital/pendingRecord',                component: resolve => require(['@/components/hospital/children/routine/pendingRecord'], resolve)},       //常规病历---待诊病历
        {path: '/hospital/alreadyRecord',                component: resolve => require(['@/components/hospital/children/routine/alreadyRecord'], resolve)},       //常规病历---已诊病历
        {path: '/hospital/retreatRecord',                component: resolve => require(['@/components/hospital/children/routine/retreatRecord'], resolve)},       //常规病历---退回病历
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/hospital/addConsult',                   component: resolve => require(['@/components/hospital/children/consult/addConsult'], resolve)},          //会诊病历---新建会诊
        {path: '/hospital/InProcessConsult',             component: resolve => require(['@/components/hospital/children/consult/InProcessConsult'], resolve)},    //会诊病历---处理中会诊
        {path: '/hospital/alreadyConsult',               component: resolve => require(['@/components/hospital/children/consult/alreadyConsult'], resolve)},      //会诊病历---已处理会诊
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/hospital/pendingYYFrozen',              component: resolve => require(['@/components/hospital/children/frozen/pendingYYFrozen'], resolve)},      //冰冻病历---待预约冰冻
        {path: '/hospital/pendingCLFrozen',              component: resolve => require(['@/components/hospital/children/frozen/pendingCLFrozen'], resolve)},      //冰冻病历---待处理冰冻
        {path: '/hospital/alreadyHandle',                component: resolve => require(['@/components/hospital/children/frozen/alreadyHandle'], resolve)},        //冰冻病历---已处理冰冻
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/hospital/statistics',                   component: resolve => require(['@/components/hospital/children/statistics/statistics'], resolve)},       //统计查询---统计
        {path: '/hospital/query',                        component: resolve => require(['@/components/hospital/children/statistics/query'], resolve)},            //统计查询---查询
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/hospital/templateMaintain',             component: resolve => require(['@/components/hospital/children/templateMaintain/templateMaintain'], resolve)},//模板维护---模板维护
      ]
    },
    {
      path: '/expert',//专家端
      component: resolve => require(['@/components/expert/expert'], resolve),
      children: [
        {path: '/expert',                                component: resolve => require(['@/components/expert/children/schedule/schedule'], resolve)},              //日程安排
        {path: '/expert/schedule',                       component: resolve => require(['@/components/expert/children/schedule/schedule'], resolve)},              //日程安排--日程安排
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/expert/stayDiagnosis',                  component: resolve => require(['@/components/expert/children/routine/stayDiagnosis'], resolve)},          //常规病历--待诊病历
        {path: '/expert/alreadyDiagnosis',               component: resolve => require(['@/components/expert/children/routine/alreadyDiagnosis'], resolve)},       //常规病历--已诊病历
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/expert/beingProcessed',                 component: resolve => require(['@/components/expert/children/consult/beingProcessed'], resolve)},         //会诊病历--处理中
        {path: '/expert/alreadyHandleConsult',           component: resolve => require(['@/components/expert/children/consult/alreadyHandle'], resolve)},          //会诊病历--已处理
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/expert/frozenOrder',                    component: resolve => require(['@/components/expert/children/frozen/frozenOrder'], resolve)},             //冰冻病历--冰冻预约
        {path: '/expert/stayHandle',                     component: resolve => require(['@/components/expert/children/frozen/stayHandle'], resolve)},              //冰冻病历---待处理冰冻
        {path: '/expert/alreadyHandleFrozen',            component: resolve => require(['@/components/expert/children/frozen/alreadyHandle'], resolve)},           //冰冻病历---已处理冰冻
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/expert/statistics',                     component: resolve => require(['@/components/expert/children/statistics/statistics'], resolve)},          //统计查询---统计
        {path: '/expert/query',                          component: resolve => require(['@/components/expert/children/statistics/query'], resolve)},               //统计查询---查询
      ]
    },
    {
      path: '/allocating',//分配端
      component: resolve => require(['@/components/allocating/allocating'], resolve),
      children: [
        {path: '/allocating',                            component: resolve => require(['@/components/allocating/children/pending/stayDistributionBL'], resolve)},       //待处理病历
        {path: '/allocating/stayDistributionBL',         component: resolve => require(['@/components/allocating/children/pending/stayDistributionBL'], resolve)},       //待处理病历--待分配病历
        {path: '/allocating/ReferralApply',              component: resolve => require(['@/components/allocating/children/pending/ReferralApply'], resolve)},            //待处理病历--转诊申请
        {path: '/allocating/repeatApply',                component: resolve => require(['@/components/allocating/children/pending/repeatApply'], resolve)},              //待处理病历--复审申请
        {path: '/allocating/delayApply',                 component: resolve => require(['@/components/allocating/children/pending/delayApply'], resolve)},               //待处理病历--延迟申请
        {path: '/allocating/overtimeRecord',             component: resolve => require(['@/components/allocating/children/pending/overtimeRecord'], resolve)},           //待处理病历--超时病历
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/allocating/alreadyAssigned',            component: resolve => require(['@/components/allocating/children/alreadyAssigned/alreadyAssigned'], resolve)},  //已分配病历--已分配病历
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/allocating/frozenYY',                   component: resolve => require(['@/components/allocating/children/frozen/frozenYY'], resolve)},                  //冰冻--冰冻预约
        {path: '/allocating/frozenYYSuccess',            component: resolve => require(['@/components/allocating/children/frozen/frozenYYSuccess'], resolve)},           //冰冻--预约成功
        {path: '/allocating/frozenInvitation',           component: resolve => require(['@/components/allocating/children/frozen/frozenInvitation'], resolve)},          //冰冻--冰冻邀请
        {path: '/allocating/CompletedFrozen',            component: resolve => require(['@/components/allocating/children/frozen/CompletedFrozen'], resolve)},           //冰冻--已完成冰冻
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/allocating/stayDistributionHZ',         component: resolve => require(['@/components/allocating/children/consult/stayDistributionHZ'], resolve)},       //会诊--待分配
        {path: '/allocating/HZInvitation',               component: resolve => require(['@/components/allocating/children/consult/HZInvitation'], resolve)},             //会诊--会诊邀请
        {path: '/allocating/HZing',                      component: resolve => require(['@/components/allocating/children/consult/HZing'], resolve)},                    //会诊--会诊中
        {path: '/allocating/alreadyHZ',                  component: resolve => require(['@/components/allocating/children/consult/alreadyHZ'], resolve)},                //会诊--已会诊
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/allocating/statistics',                 component: resolve => require(['@/components/allocating/children/statistics/statistics'], resolve)},            //统计查询--统计
        {path: '/allocating/query',                      component: resolve => require(['@/components/allocating/children/statistics/query'], resolve)},                 //统计查询--查询
      ]
    },
    {
      path: '/release',//发布端
      component: resolve => require(['@/components/release/release'], resolve),
      children: [
        {path: '/release',                               component: resolve => require(['@/components/release/children/stayRelease'], resolve)},                              // 发布端
        {path: '/release/stayRelease',                   component: resolve => require(['@/components/release/children/stayRelease'], resolve)},                              // 发布端--待发布
        {path: '/release/alreadyRelease',                component: resolve => require(['@/components/release/children/alreadyRelease'], resolve)},                           // 发布端--已发布
        {path: '/release/againRelease',                  component: resolve => require(['@/components/release/children/againRelease'], resolve)},                             // 发布端--重新发布
      ]
    },
    {
      path: '/outlay',//计费端
      component: resolve => require(['@/components/outlay/outlay'], resolve),
      children: [
        {path: '/outlay',                                component: resolve => require(['@/components/outlay/children/statistics/statistics'], resolve)},                      // 计费端
        {path: '/outlay/statistics',                     component: resolve => require(['@/components/outlay/children/statistics/statistics'], resolve)},                      // 计费端--统计
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/outlay/query',                          component: resolve => require(['@/components/outlay/children/query/query'], resolve)},                                // 计费端--查询
        {path: '/outlay/staySettlement',                 component: resolve => require(['@/components/outlay/children/settlement/staySettlement'], resolve)},                  // 计费端--结算--待结算
        /*-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
        {path: '/outlay/alreadySettlement',              component: resolve => require(['@/components/outlay/children/settlement/alreadySettlement'], resolve)},               // 计费端--结算--已结算
      ]
    },

    {
      path: '/manage/superManage',//管理端--超级管理员
      component: resolve => require(['@/components/manage/superManage/superManage'], resolve),
      children: [
        {path: '/manage/superManage',                    component: resolve => require(['@/components/manage/superManage/children/userManage'], resolve)},              // 管理端--超级管理员
        {path: '/manage/superManage/userManage',         component: resolve => require(['@/components/manage/superManage/children/userManage'], resolve)},              // 管理端--超级管理员--用户管理
        {path: '/manage/superManage/applyManage',        component: resolve => require(['@/components/manage/superManage/children/applyManage'], resolve)},             // 管理端--超级管理员--申请管理
        {path: '/manage/superManage/statistics',         component: resolve => require(['@/components/manage/superManage/children/statistics'], resolve)},              // 管理端--超级管理员--统计
        {path: '/manage/superManage/query',              component: resolve => require(['@/components/manage/superManage/children/query'], resolve)},                   // 管理端--超级管理员--查询
      ]
    },
    {
      path: '/manage/ordinaryManage',//管理端--普通管理员
      component: resolve => require(['@/components/manage/ordinaryManage/ordinaryManage'], resolve),
      children: [
        {path: '/manage/ordinaryManage',                 component: resolve => require(['@/components/manage/ordinaryManage/children/hospitalManage'], resolve)},        // 管理端--普通管理员
        {path: '/manage/ordinaryManage/hospitalManage',  component: resolve => require(['@/components/manage/ordinaryManage/children/hospitalManage'], resolve)},        // 管理端--普通管理员--医院端管理
        {path: '/manage/ordinaryManage/expertManage',    component: resolve => require(['@/components/manage/ordinaryManage/children/expertManage'], resolve)},          // 管理端--普通管理员--专家端管理
        {path: '/manage/ordinaryManage/allocatingManage',component: resolve => require(['@/components/manage/ordinaryManage/children/allocatingManage'], resolve)},      // 管理端--普通管理员--分配端管理
        {path: '/manage/ordinaryManage/releaseManage',   component: resolve => require(['@/components/manage/ordinaryManage/children/releaseManage'], resolve)},         // 管理端--普通管理员--发布端管理
        {path: '/manage/ordinaryManage/outlayManage',    component: resolve => require(['@/components/manage/ordinaryManage/children/outlayManage'], resolve)},          // 管理端--普通管理员--计费端管理
        {path: '/manage/ordinaryManage/CostManage',      component: resolve => require(['@/components/manage/ordinaryManage/children/CostManage'], resolve)},            // 管理端--普通管理员--计费管理
        {path: '/manage/ordinaryManage/templateManage',  component: resolve => require(['@/components/manage/ordinaryManage/children/templateManage'], resolve)},        // 管理端--普通管理员--模板管理
        {path: '/manage/ordinaryManage/statistics',      component: resolve => require(['@/components/manage/ordinaryManage/children/statistics'], resolve)},            // 管理端--普通管理员--统计
        {path: '/manage/ordinaryManage/query',           component: resolve => require(['@/components/manage/ordinaryManage/children/query'], resolve)},                 // 管理端--普通管理员--查询
      ]
    }
  ]
})



